html{
    height:100vh;
}
#root{
    height:100vh;
    background-color: #bbe0e4;
}
#myBox{
    background-color: aliceblue;
}
#mode{
    position:fixed;
    top:4vw;
    right:1vw;
}
#mode input:hover{
    background-color: rgb(197, 230, 178);
}
.btn-info:hover{
    background-color: rgb(152, 26, 97);
    color:yellow;
}
.alert-dismissible{
    position:fixed;
    width:100vw;
    height:55px;
}
.alert-dismissible strong{
    position: relative;
    bottom:6px;
}
.colorPalette{
    display:inline-block;
    width:2vw;
    height:2vw;
    margin:5px 3px;
    border:1px solid black;
    border-radius:100px;
    cursor: pointer;
}
.colorPalette:hover{
    box-shadow: 0px 0px 20px yellow ;
    transform: scale(1.1);
}
#blueColor{
    background-color: blue;
}
#greenColor{
    background-color: green;
}
#purpleColor{
    background-color: purple;
}
#About{
    display:flex;
    justify-content: space-around;
}
#inner{
    margin-top:2.5rem;
}
.card img{
    aspect-ratio: 3/2;
    object-fit: contain;
    border-radius: 20px;
}
.card:hover{
    transform: scale(1.1);
    box-shadow: 0px 0px 10px yellowgreen;
    transition: all 0.2s ease-in-out 0s;
    cursor: pointer;
}
#service{
    text-align: center;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 4vw;
    font-style: italic;
}